import { Component, Prop } from 'vue-property-decorator'
import styles from './index.module.scss'
import BaseView from '../../base/view'
import BaseComponent from '../../base'
import { getArgsFromUrlHash } from '@badam/common-utils'

interface Props {}

@Component({})
class Toast extends BaseComponent<Props> {
  @Prop()
  text!: string

  @Prop()
  fontSize!: number

  @Prop()
  fontFamily!: string

  @Prop()
  bottom!: string

  showWrap = true

  showContent = true

  render(): JSX.Element | null {
    return this.showWrap ? (
      <div
        dir="auto"
        style={{
          fontSize: `${this.fontSize}px`,
          fontFamily: this.fontFamily || 'notomid',
          bottom: this.bottom,
        }}
        class={[styles.wrap, this.showContent ? styles.fadeIn : styles.fadeOut]}
      >
        {this.text}
      </div>
    ) : null
  }
}

export const showToast = (
  text: string,
  duration = 4000,
  fontFamily?: string,
  fontSize?: number,
  bottom?: string
) => {
  const isSptu = getArgsFromUrlHash('is_open_sptu') && getArgsFromUrlHash('sptu_opened')
  const defaultFont = isSptu ? 'notomid-sptu' : 'notomid'
  const toast = new Toast({ el: document.createElement('div') }) as any
  const el = toast.$el
  toast.text = text
  toast.fontSize = fontSize
  toast.fontFamily = fontFamily || defaultFont
  toast.bottom = bottom
  document.body.appendChild(el)
  //  250ms 
  setTimeout(() => {
    toast.showContent = false
  }, duration - 1250)
  //  duration 
  setTimeout(() => {
    toast.showWrap = false
  }, duration)
  return toast
}

export default Toast

export const compName = ''
