/* eslint-disable @typescript-eslint/no-empty-function */
/* eslint-disable no-param-reassign */
import { Component, Prop } from 'vue-property-decorator'
import * as tsx from '@badam/vue-tsx-support'
import { VueConstructor } from 'vue'

interface Props {
  listenLoaded?: (elm?: HTMLElement) => any
  isLoading?: boolean
}

@Component({})
class Provider extends tsx.Component<Props> {
  @Prop({ default: () => {}, type: Function })
  listenLoaded!: (elm?: HTMLElement) => any

  @Prop({ default: false })
  isLoading!: boolean

  elmTimer: any = 0

  elmDelay = 16

  static compName = 'Provider'

  static install: (Vue: VueConstructor) => void

  awaitElm(def: any, cb: any) {
    const elm: HTMLElement = (def?.elm || null) as HTMLElement
    if (!elm) {
      clearTimeout(this.elmTimer)
      this.elmTimer = setTimeout(() => {
        this.awaitElm(def, cb)
      }, this.elmDelay)
    } else {
      cb(elm)
    }
  }

  render(): any {
    if (this.$slots.default && this.$slots.default.length > 1) {
      throw new Error('Provider')
    }
    const inst = this.$slots.default ? this.$slots.default[0] : null
    const def = inst
    this.awaitElm(def, (elm: HTMLElement) => {
      this.listenLoaded(elm)
    })
    return def
  }
}

Provider.install = (Vue) => {
  Vue.component(Provider.compName, Provider)
}

export default Provider
