/* eslint-disable import/no-cycle */
/* eslint-disable @typescript-eslint/no-this-alias */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-param-reassign */
/* eslint-disable no-use-before-define */
/* eslint-disable @typescript-eslint/no-empty-function */
import { Component, Prop } from 'vue-property-decorator'
import * as tsx from '@badam/vue-tsx-support'
import { VueConstructor } from 'vue'
import Provider from './provider'

type GlobalRegister = {
  listenLoaded?: (elm?: HTMLElement) => any
  isLoading?: boolean
}

@Component({})
export default class BaseComponent<P = unknown, E = unknown, S = unknown> extends tsx.Component<
  P & GlobalRegister,
  E,
  S
> {
  @Prop({ default: () => {}, type: Function })
  listenLoaded!: (elm?: HTMLElement) => any

  @Prop({ default: false })
  isLoading!: boolean

  mounted(): void {}

  created(): void {}

  destroyed(): void {}

  static install: (Vue: VueConstructor) => void

  static compName = 'BaseComponent'

  // 
  r(): JSX.Element | null {
    return null
  }

  render(): JSX.Element | null {
    return (
      <Provider listenLoaded={this.listenLoaded} isLoading={this.isLoading}>
        {this.r()}
      </Provider>
    )
  }
}

export function convertTsx<P>(comp: any) {
  return tsx.ofType<P>().convert(comp)
}

BaseComponent.install = (Vue) => {
  Vue.component(BaseComponent.compName, BaseComponent)
}
